  import moduleWrap from 'components/moduleWrap'
  import singleBar from 'components/echarts/knownSingleBar'
  import timelineBar from 'components/echarts/knownTimelineBar'
  import typePie from 'components/echarts/knownRiskPie'
  import personPie from 'components/echarts/knownRiskPie'
  import knownMediaTypePie from 'components/echarts/knownMediaTypePie.vue'
  import knownMediaNegativePie from 'components/echarts/knownMediaNegativePie.vue'
  import elPageation from 'components/pagination/pagination'

  export default {
    name: 'knowCelebrity',
    data () {
      return {
        //
        pageSize: 6,
        total: 30,
        pageCount: 5,
        layout: 'total,prev, pager,next',
        page: 1,
        //
        riskNumberData: {//
          xAxisData: [],
          timeLinedata: [],
          seriesData: [],
          data: [],
          option: {}
        },
        riskTypeData: {//
          legendData: [],
          seriesData: [],
          data: [],
          option: {}
        },
        riskPersonData: {//
          legendData: [],
          seriesData: [],
          data: [],
          option: {}
        },
        mediaNumberData: {//
          xAxisData: [],
          seriesData: [],
          data: [],
          option: {}
        },
        mediaTypeData: {//
          xAxisData: [],
          data: [],
          option: {}
        },
        mediaPersonData: {//
          xAxisData: [],
          data: [],
          option: {}
        },
        //
        TopManagerData: [],
        //investments
        InvestmentsDatas: [],
        //
        ShareholderDatas: [],
        //
        KnownListDatas: []
      }
    },
    mounted () {
      //
      this.getriskNumberData()
      //
      this.getriskTypeData()
      //
      this.getriskPersonData()
      //
      this.getmediaNumberData()
      //
      this.getmediaTypeData()
      //
      this.getmediaPersonData()
      //
      this.getTopManagerData()
      //investments
      this.getInvestmentsData()
      //
      this.getShareholderData()
      //
      this.getKnownListData()
    },
    methods: {
      //
      pageChange (val) {
        this.page = val
        console.log(` ${val} ${this.pageSize}`)
      },
      //
      getriskNumberData () {
        this.riskNumberData.xAxisData = ['2012', '2013', '2014', '2015', '2016', '2017']
        this.riskNumberData.timeLinedata = ['2012', '2013', '2014', '2015', '2016', '2017']
        this.riskNumberData.seriesData = [20, 30, 40, 20, 30, 90, 50, 60]
      },
      //
      getriskTypeData () {
        this.riskTypeData.legendData = ['', '', '']
        this.riskTypeData.seriesData = [{value: 25, name: ''}, {value: 35, name: ''}, {value: 40, name: ''}]
      },
      //
      getriskPersonData () {
        this.riskPersonData.legendData = ['', '', '']
        this.riskPersonData.seriesData = [{value: 53, name: ''}, {value: 12, name: ''}, {value: 45, name: ''}]
      },
      //
      getmediaNumberData () {
        this.mediaNumberData.xAxisData = ['', '', '', '', '', '', '', '']
        this.mediaNumberData.seriesData = [20, 30, 40, 20, 30, 90, 50, 60]
      },
      //
      getmediaTypeData () {
        this.mediaTypeData.legendData = ['', '', '', '', '', '', '']
        this.mediaTypeData.seriesData = [{value: 35, name: ''}, {value: 16, name: ''}, {value: 11, name: ''}, {value: 11, name: ''}, {value: 11, name: ''}, {value: 8, name: ''}, {value: 8, name: ''}]
      },
      //
      getmediaPersonData () {
        this.mediaPersonData.legendData = ['', '', '', '', '', '']
        this.mediaPersonData.seriesData = [{value: 100, name: ''}, {value: 120, name: ''}, {value: 150, name: ''}, {value: 180, name: ''}, {value: 220, name: ''}, {value: 300, name: ''}, {
          value: 1070,
          name: '',
          label: {
            normal: {
              show: false
            },
            emphasis: {
              show: false
            }
          },
          tooltip: {
            formatter: function (a) {
              return ''
            }
          }
        }]
      },
      //
      getTopManagerData () {
        let topManagerDatas = [
          {'company': '', 'position': '', 'area': '', 'foundDate': '2017.11.26', 'registeredFund': '2017.8', 'state': '', 'stateNum': '0', 'riskWarning': '6'},
          {'company': '', 'position': '', 'area': '', 'foundDate': '2017.11.26', 'registeredFund': '1918.8', 'state': '', 'stateNum': '1', 'riskWarning': '6'},
          {'company': '', 'position': '', 'area': '', 'foundDate': '2017.11.26', 'registeredFund': '1818.8', 'state': '', 'stateNum': '2', 'riskWarning': '6'},
          {'company': '', 'position': '', 'area': '', 'foundDate': '2017.11.26', 'registeredFund': '1718.8', 'state': '', 'stateNum': '0', 'riskWarning': '6'},
          {'company': '', 'position': '', 'area': '', 'foundDate': '2017.11.26', 'registeredFund': '1618.8', 'state': '', 'stateNum': '0', 'riskWarning': '6'}]
        this.TopManagerData = topManagerDatas
      },
      //
      getInvestmentsData () {
        let InvestmentsDatas = [
          {'company': '', 'legalPerson': '', 'area': '', 'registeredFund': '1917.2', 'foundDate': '2016.10.16', 'state': '', 'stateNum': '0'},
          {'company': '', 'legalPerson': '', 'area': '', 'registeredFund': '1812.8', 'foundDate': '2013.08.20', 'state': '', 'stateNum': '0'},
          {'company': '', 'legalPerson': '', 'area': '', 'registeredFund': '1716.3', 'foundDate': '2015.11.26', 'state': '', 'stateNum': '1'},
          {'company': '', 'legalPerson': '', 'area': '', 'registeredFund': '1517.8', 'foundDate': '2017.12.26', 'state': '', 'stateNum': '1'},
          {'company': '', 'legalPerson': '', 'area': '', 'registeredFund': '1215.9', 'foundDate': '2016.07.15', 'state': '', 'stateNum': '2'},
        ]
        this.InvestmentsDatas = InvestmentsDatas
      },
      //
      getShareholderData () {
        let ShareholderDatas = [
          {'shareHolder': '', 'shareHolderType': '', 'shareHolderCard': '', 'shareHolderNum': '91641100MA75W5H94C'},
          {'shareHolder': '', 'shareHolderType': '', 'shareHolderCard': '', 'shareHolderNum': '91641100MA75W5H94C'},
          {'shareHolder': '', 'shareHolderType': '', 'shareHolderCard': '', 'shareHolderNum': '91641100MA75W5H94C'},
          {'shareHolder': '', 'shareHolderType': '', 'shareHolderCard': '', 'shareHolderNum': '91641100MA75W5H94C'},
          {'shareHolder': '', 'shareHolderType': '', 'shareHolderCard': '', 'shareHolderNum': '91641100MA75W5H94C'},
        ]
        this.ShareholderDatas = ShareholderDatas
      },
      //
      getKnownListData () {
        let KnownListDatas = [
          {
            'company': '',
            'legalPerson': '   ',
            'emile': '3139955637@qq.com',
            'phone': '025-52466516',
            'area': '5778801-810',
            'registeredFund': '2005.2',
            'foundDate': '1-3',
            'trades': '',
            'publicSector': ''
          },
          {
            'company': '',
            'legalPerson': '   ',
            'emile': '3139955637@qq.com',
            'phone': '025-52466516',
            'area': '5778801-810',
            'registeredFund': '5000.2',
            'foundDate': '1-2',
            'trades': '',
            'publicSector': ''
          },
          {
            'company': '',
            'legalPerson': '',
            'emile': '3139955637@qq.com',
            'phone': '025-52466516',
            'area': '5778801-810',
            'registeredFund': '3000.2',
            'foundDate': '1-6',
            'trades': '',
            'publicSector': ''
          },
          {
            'company': '',
            'legalPerson': '   ',
            'emile': '3139955637@qq.com',
            'phone': '13703378912',
            'area': '5778801-810',
            'registeredFund': '1917.2',
            'foundDate': '1-5',
            'trades': '',
            'publicSector': ''
          },
          {
            'company': '',
            'legalPerson': '',
            'emile': '3139955637@qq.com',
            'phone': '025-52466516',
            'area': '5778801-810',
            'registeredFund': '2915.2',
            'foundDate': '1-4',
            'trades': '',
            'publicSector': ''
          },
          {
            'company': '',
            'legalPerson': '   ',
            'emile': '3139955637@qq.com',
            'phone': '025-52466516',
            'area': '5778801-810',
            'registeredFund': '2000',
            'foundDate': '1-3',
            'trades': '',
            'publicSector': ''
          }
        ]
        this.KnownListDatas = KnownListDatas
      },
      // KnownListDatas   registeredFundfoundDate
      compares (property, type) {
        if (type == 'just') {
          return function (a, b) {
            var value1 = a[property]
            var value2 = b[property]
            return value2 > value1
          }
        } else {
          return function (a, b) {
            var value1 = a[property]
            var value2 = b[property]
            return value1 > value2
          }
        }
      },
      sortTable (e) {
        //ajax
        if (e.target.classList[1] == 'reverse') {
          e.target.classList.remove('reverse')
          e.target.classList.add('just')
          this.KnownListDatas.sort(this.compares('registeredFund', 'just'))
         // console.log(this.KnownListDatas)
        } else {
          e.target.classList.remove('just')
          e.target.classList.add('reverse')
          this.KnownListDatas.sort(this.compares('registeredFund', 'reverse'))
          //console.log(this.KnownListDatas)
        }
      },
      sortTable2 (e) {
        //ajax
        if (e.target.classList[1] == 'reverse') {
          e.target.classList.remove('reverse')
          e.target.classList.add('just')
          this.KnownListDatas.sort(this.compares('foundDate', 'just'))
         // console.log(this.KnownListDatas)
        } else {
          e.target.classList.remove('just')
          e.target.classList.add('reverse')
          this.KnownListDatas.sort(this.compares('foundDate', 'reverse'))
         // console.log(this.KnownListDatas)
        }
      }
    },
    components: {
      moduleWrap,
      singleBar,
      timelineBar,
      typePie,
      personPie,
      knownMediaTypePie,
      knownMediaNegativePie,
      elPageation
    }
