</script>

<style lang="scss" scoped>

  //
  .table-div {
    width: 1170px;
    height: 235px;
  }

  //
  .table-wrap {
    width: 100%;
    height: 100%;
  }

  .thead tr th {
    height: 38px;
    font-size: 14px;
    text-align: center;
    background: #f2f1f1;
    border: 1px solid #e2e2e2;
  }

  .table-wrap tr:hover {
    background: #f2f1f1;
  }

  //
  .table-wrap tr td {
    border: 1px solid #e2e2e2;
    text-align: center;
    .fund {
      color: #ff7e00;
      font-size: 16px;
    }
    .state0 {
      color: #19c111;
    }
    .state1 {
      color: #f54b00;
    }
    .state2 {
      color: #ff9000;
    }
  }

  .table-wrap tr .company {
    text-align: left;
    width: 30%;
    padding-left: 60px;
    box-sizing: border-box;
  }

  //
  .topManager tr td:nth-child(3), .topManager tr td:nth-child(4) {
    width: 10%;
  }

  //
  .investments tr td:nth-child(2), .investments tr td:nth-child(3) {
    width: 10%;
  }

  .investments tr td:nth-child(4) {
    width: 20%;
  }

  //
  .shareholder tr td:nth-child(2), .shareholder tr td:nth-child(3) {
    width: 20%;
  }

  //
  .echarts-div {
    width: 1170px;
    height: 345px;
    display: flex;
    flex-direction: row;
    .number, .type, .person {
      border: 1px solid #e2e2e2;
      width: 100%;
      height: 280px;
    }
    .knownMediaNegative {
      background: url("../../assets/img/knownMediaNegativePie.png") no-repeat;
      background-position: center;
      background-size: cover;
    }
  }

  .echarts-div > div:nth-child(1) {
    flex: 2;
    margin-right: 15px;
  }

  .echarts-div > div:nth-child(2) {
    margin-right: 15px;
  }

  .echarts-div > div:nth-child(2), .echarts-div > div:nth-child(3) {
    flex: 1;
  }

  //
  .knownList {
    height: 1180px;
    width: 1170px;
    border: 1px solid #e2e2e2;
    table-layout: fixed;
    padding: 10px 20px 20px;
    box-sizing: border-box;
    .handle-btn {
      a {
        display: block;
        color: #015293;
        font-size: 14px;
        height: 32px;
        text-decoration: underline;
        cursor: pointer;
      }
      a:hover {
        color: #f52e14;
      }
    }
    .fund {
      color: #ff7e00;
      font-size: 24px;
    }
    .table {
      width: 100%;
      height: 1088px;
    }
    .table th, .table td {
      text-align: center;
      table-layout: fixed;
    }
    .table tr td {
      height: 162px;
    }
    .table tr td:nth-child(1), .table tr th:nth-child(1) {
      width: 485px;
    }
    .table th {
      color: #474747;
      font-size: 16px;
    }
    .table thead tr {
      height: 46px;
      line-height: 46px;
      border-bottom: 1px solid #cccccc;
    }
    .table tbody tr {
      border-bottom: 1px dashed #cccccc;
    }
    .table tr td div p {
      line-height: 34px;
    }
    .table tr td div p span {
      color: #999;
      font-size: 14px;
    }
    .table tr td div p span:nth-child(2) {
      color: #474747;
      font-size: 14px;
    }
    .entName, .address, .phone {
      width: 100%;
      text-align: left;
      font-size: 16px;
      color: #4c4c4c;
    }
    .name-emile {
      display: flex;
      justify-content: space-between;
    }
    .table th.sort {
      position: relative;
      cursor: pointer;
    }
    .table th.sort::after, .table th.sort::before {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
    }
    .table th.sort::after, .table th.sort::before {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
    }
    .table th.sort::before {
      border-right: 4px solid transparent;
      border-bottom: 6px solid #ccc;
      border-left: 4px solid transparent;
      top: 18px;
      right: 10px;
    }
    .table th.sort::after {
      border-top: 6px solid #ccc;
      border-right: 4px solid transparent;
      border-left: 4px solid transparent;
      top: 25px;
      right: 10px;
    }
    .table th.just::before {
      border-right: 4px solid transparent;
      border-bottom: 6px solid #015293;
      border-left: 4px solid transparent;
      top: 18px;
      right: 10px;
    }
    .table th.just::after {
      border-top: 6px solid #ccc;
      border-right: 4px solid transparent;
      border-left: 4px solid transparent;
      top: 25px;
      right: 10px;
    }
    .table th.reverse::before {
      border-right: 4px solid transparent;
      border-bottom: 6px solid #ccc;
      border-left: 4px solid transparent;
      top: 18px;
      right: 10px;
    }
    .table th.reverse::after {
      border-top: 6px solid #015293;
      border-right: 4px solid transparent;
      border-left: 4px solid transparent;
      top: 25px;
      right: 10px;
    }
  }
</style>



