<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
      return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {}
      }
    },
    watch: {
      echartsData: {
        handler: function(val, oldVal) {
          this.initChart()
        },
        deep: true //deep 
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart() {
        let option = {
          tooltip: {
            //trigger: 'axis',
            axisPointer: { // 
              type: 'shadow', // 'line' | 'shadow'
              shadowStyle: {
                color: '#eeeff1',
                shadowBlur: 0,
                shadowColor: '#ff9014',
                shadowOffsetY: -5
              },
              z: 0
            },
            backgroundColor: 'transparent',
            formatter: function (param) {
              let html = '';
              return html += '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border:1px solid rgba(211,221,231,.9);border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);">' +
                '<p style="font-size:16px;font-weight:bold;">' + param.data.name +

                '<span stype="color:rgba(211,221,231,.9);font-size:20px;">' + param.data.value + '</span>' +
                '</p>' +
                '</div>'
            }
          },
          backgroundColor: '#fff',
          legend: {
            show: false,
            x: 20,
            y2: 20,
            orient: 'vertical',
            //data: ["", "", "","", "", "", ""]
            data: this.echartsData.legendData
          },
          color:['#397cd8', '#2d9bf8', '#19bdff', '#53d02b', '#ffc600','#ff8e14','#fb6e15'],
          calculable: false,
          series: [{
            name:'',
            type:'pie',
            //selectedMode: 'single',
            radius: ['25%', '45%'],
            center: ['50%', '50%'],

            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            },
            label: {
              normal: {
                position: 'outside',
                show: true,
                formatter: "{b}",
                textStyle: {
                  color: "#666",
                  fontSize: 14
                }
              }
            },
            labelLine: {
              normal: {
                length:10,
                length2:10,
                /*lineStyle: {
                    color: "#666"
                }*/
              }
            },
            /*data: [{
              value: 35,
              name: ''
            }, {
              value: 16,
              name: ''
            }, {
              value: 11,
              name: ''
            },
            {
              value: 11,
              name: ''
            }, {
              value: 11,
              name: ''
            }, {
              value: 8,
              name: ''
            }, {
              value: 8,
              name: ''
            }]*/
            data:this.echartsData.seriesData
          }]
        }
        this.myChart.setOption(option)
      }
    },
    watch: {}
  }
</script>

<style lang="scss" scoped>
  .echarts {
    width: 100%;
    height: 100%;
  }
</style>



