<template>
  <div id="listedMap"></div>
</template>
<script>
import echarts from 'echarts'
import china from '../../../assets/js/geojs/china.json'
export default {
  methods: {
    createChart () {
      let chart = echarts.init(document.getElementById('listedMap'))
      echarts.registerMap('china', china)
      let option = {
        backgroundColor: '#fff',
        /* tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(0,0,0,.5)',
          show: true,
          padding: [10, 18, 10, 18],
          formatter: function (datas) {
            return datas.value + '<br/>' + datas.name
          },
          textStyle: {
            color: '#fff',
            align: 'center'
          }

        }, */
        visualMap: {
          type: 'piecewise',
          pieces: [
            { gt: 1500 },
            { gt: 900, lte: 1500 },
            { gt: 310, lte: 1000 },
            { gt: 200, lte: 300 },
            { gt: 0, lte: 200 }
          ],
          left: 'left',
          bottom: '5%',           // 
          calculable: true,
          padding: [0, 0, 0, 50],
          color: ['#2998FF', '#4EAAFF', '#64C3FF', '#CEE7FF'],
          itemWidth: 10,
          textStyle: {
            color: '#4FA9FF'
          }
        },
        series: [
          {
            type: 'map',
            mapType: 'china',
            itemStyle: {
              normal: {
                areaColor: '#C1E0FF',
                borderColor: '#fff',
                borderWidth: 1,
                borderType: 'solid',
                label: {
                  show: true,
                  textStyle: {
                    color: '#000'
                  }
                }
              },
              emphasis: {
                areaColor: '#FFAA25',
                label: {
                  show: true
                }
              }
            },
            data: [
              { name: '', value: 3 },
              { name: '', value: 4 },
              { name: '', value: 1 },
              { name: '', value: 3 },
              { name: '', value: 200 },
              { name: '', value: 1 },
              { name: '', value: 2 },
              { name: '', value: 1000 },
              { name: '', value: 1 },
              { name: '', value: 100 },
              { name: '', value: 1000 },
              { name: '', value: 200 },
              { name: '', value: 230 },
              { name: '', value: 1 },
              { name: '', value: 1200 },
              { name: '', value: 200 },
              { name: '', value: 600 },
              { name: '', value: 140 },
              { name: '', value: 2 },
              { name: '', value: 200 },
              { name: '', value: 500 },
              { name: '', value: 2000 },
              { name: '', value: 2 },
              { name: '', value: 70 },
              { name: '', value: 1 },
              { name: '', value: 2 },
              { name: '', value: 100 },
              { name: '', value: 100 },
              { name: '', value: 2000 },
              { name: '', value: 1 },
              { name: '', value: 2 },
              { name: '', value: 1000 },
              { name: '', value: 1 },
              { name: '', value: 2 }
            ]
          }
        ]
      }
      // window.onresize = chart.resize
      chart.setOption(option)
    }
  },
  mounted () {
    this.createChart()
  }
}
</script>
<style>
#listedMap {
  width: 80%;
  height: 100%;
}
</style>



