              sentiment: '66',
              judicial: '66',
              penalty: '66'
            }
          ]
        this.tableData = tableData
      },
      compares (property,type) {
          if(type == 'just') {
            return function(a, b) {
              var value1 = a[property];
              var value2 = b[property];
              return value2 > value1;
            }
          }else{
            return function(a, b) {
              var value1 = a[property];
              var value2 = b[property];
              return value1 > value2;
            }
          }
      },
      sortTable(e) {
         //ajax
        if(e.target.classList[1] == 'reverse'){
          e.target.classList.remove('reverse')
          e.target.classList.add('just')
          this.tableData.sort( this.compares('limit', 'just') )
          console.log(this.tableData)
        }else{
          e.target.classList.remove('just')
          e.target.classList.add('reverse')
          this.tableData.sort( this.compares('limit', 'reverse') )
          console.log(this.tableData)
        }
      },
