        if(e.target.classList[1] == 'reverse'){
          e.target.classList.remove('reverse')
          e.target.classList.add('just')
          this.tableData.sort( this.compares('sentiment', 'just') )
          console.log(this.tableData)
        }else{
          e.target.classList.remove('just')
          e.target.classList.add('reverse')
